@import "reset";
@import "colors";

$cor-primaria: #005c91;
$cor-fundo: #fff;
$font-primaria: monospace;
$texto-botao: #fff;
$cor-texto: #001f3a;
$gutter: 20px;

body {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  background: $cor-fundo;
}

main {
  max-width: 500px;
}

// h1 {
//   color: $cor-primaria;
//   font-family: $font-primaria;
// }

a {
  text-decoration: none;
  text-transform: uppercase;
  background: $cor-primaria;
  padding: 10px;
  border-radius: 8px;
  color: $texto-botao;
  font-weight: bold;
  font-family: $font-primaria;
}

p {
  color: $cor-texto;
  font-size: 18px;
  font-weight: 400;
  font-family: $font-primaria;
  margin-bottom: $gutter;
}
